																</div>
																<div v-else
																	style="display: flex; flex-direction: column; justify-content: space-between; align-items: center; width: inherit; margin: auto;">
																	<div class="payout_Item_content"
																		style="display: flex; width: 270px; height: 48px; justify-content: space-between;">
																		<div class="payline_details"
																			style="display: flex; width: 230px; height: 48px;">
																			<div class="win_line_content"
																				style="text-align: left; margin-top: 15px;">
																				<div
																					style="font-size: 13px; color: rgba(255, 255, 255, 0.6);">
																					Multiplicador x{{item.win_mul}}
																				</div>
																			</div>
																		</div>
																		<div class="win_text_section"
																			style="display: flex; justify-content: space-between;">
																			<div class="win_text_content"
																				style="text-align: right; margin-top: 15px; width: 105px;">
																				<div
																					style="font-size: 13px; color: rgba(255, 255, 255, 0.6); text-align: end;">
																					{{ item.totalWin }}
																				</div>
																			</div>
																		</div>
																	</div>
																</div>
																<div id="tooltip" v-if="showTooltipMod"
																	:style="`position: relative; width: 300px; height: inherit; align-self: center; visibility: visible;top: ${showTooltipTop}px;`">
																	<div
																		style="left: 90%; border-width: 6px; border-style: solid; border-color: transparent transparent rgb(70, 70, 83); border-image: initial; height: 0px; width: 0px; position: absolute; pointer-events: none; bottom: 98%;">
																	</div>
																	<div id="tooltip-toast"
																		style="background-color: rgb(70, 70, 83); color: rgb(255, 255, 255); border-radius: 6px; padding: 5px;">
																		<div id="tooltip-title"
																			style="color: rgb(128, 128, 128); font-size: 12px; text-align: left; padding: 0px 8px;">
																			Bet Size x Bet Level x Symbol Payout
																			Values{{detail[i].win_mul > 1 ? ' x Multiplicador' : ''}}
																		</div>
																		<div id="tooltip-desc"
																			style="font-size: 12px; text-align: left; padding: 0px 8px;">
																			{{detail[i].betSize}} x
																			{{detail[i].bet_mul}} x
																			{{mul}}{{detail[i].win_mul > 1 ? ' x '+ detail[i].win_mul : ''}}
																		</div>
																	</div>
																</div>
															</div>
														</div>
														<div id="game-detail-padding"
															style="width: inherit; height: 86px;"></div>
													</div>
												</div>
											</vue-scroll>
										</div>
									</div>
								</div>
							</div>
						</div>
						<div id="game-details-left-arrow"
							:class="`${activeIndex <= 0 || detail.length == 1?'swiper-button-hidden':''} swiper-button-prev`"
							@click="swiper.slidePrev()"
							style="top: calc(50% - 85px); background-color: rgba(0, 0, 0, 0.4);">
							<div class="gh-angle-wrapper" style="transform: translateX(4px) scale(0.7);">
								<div class="gh-angle-horizontal angle-left" style="border-color: rgb(255, 200, 36);">
								</div>
							</div>
						</div>
						<div id="game-details-right-arrow"
							:class="`${activeIndex == (detail.length - 1) || detail.length == 1?'swiper-button-hidden':''} swiper-button-next`"
							@click="swiper.slideNext()"
							style="top: calc(50% - 85px); background-color: rgba(0, 0, 0, 0.4);">
							<div class="gh-angle-wrapper" style="transform: translateX(-4px) scale(0.7);">
								<div class="gh-angle-horizontal angle-right" style="border-color: rgb(255, 200, 36);">
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div v-show="hidePopup" :class="`${showPopup?'downPopupAnimate':'upPopupAnimate'}`"
			style="bottom: 0%; left: 0px; position: absolute; width: inherit; height:672px; z-index: 5; transform: translate3d(0px, 0px, 0px);width: 100%;">
			<div id="game-free-spin-view-container" class="history regular"
				style="background-color: #282834; color: rgba(255, 255, 255, 0.6);">
				<div class=" rcs-custom-scroll " style="height: calc(636px);">
					<div class="rcs-outer-container" style="height: 100%;">
						<div class="rcs-inner-container"
							style="height: 100%;-webkit-overflow-scrolling: touch;overflow-x: hidden;overflow-y: scroll;">
							<div style="height: 100%; overflow-y: visible; margin-right: 0px;">
								<div>
									<div class="game-free-spin-list-item" v-for="(item,i) in detail" @click="toIndex(i)"
										:key="i">
										<div class="game-free-spin-type"
											:style="activeIndex<=0 && i == 0 ?`color: rgb(255, 200, 36);` : activeIndex == i ?`color: rgb(255, 200, 36);`: `color: hsla(0,0%,100%,.6);`">
											<span>
												<span v-if="i <= 0">Normal Spin</span>
												<span v-else>Fortune Spin:{{i}}/{{detail.length-1}}</span>
											</span>
										</div>
										<div class="game-free-spin-amount"
											:style="activeIndex<=0 && i == 0 ?`color: rgb(255, 200, 36);` : activeIndex == i ?`color: rgb(255, 200, 36);`: `color: hsla(0,0%,100%,.6);`">
											{{item.profitGold}}
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
				<div id="close-list-button" @click="changePopup">Close</div>
			</div>
		</div>
		<div style="position: absolute;bottom: 0; right: 5px; line-height: 20px; font-size: 10px; padding: 2px; color: #888;z-index: 1;">1.3.36.9</div>
	</div>
</template>

<script>
	import Swiper from "swiper"
	import "swiper/swiper.min.css"

	export default {
		props: ["data", "orderNo"],
		data() {
			return {
				configurationHeight: '640',
				timezone: "(GMT+8:00)",
				showPopup: false,
				hidePopup: false,
				showTooltipMod: false,
				showTooltipTop: 0,
				mul: 0,
				activeIndex: -1,
				swiper: null,
				detail: [{
					win_info_display: [],
					localeTime: ['-', '-', '-'],
					propsList: [],
					winInfoList: [
						[false, false, false],
						[false, false, false, false],
						[false, false, false],
					],
				}],
				ops: {
					vuescroll: {},
					scrollPanel: {
						scrollingX: false, //关闭横向滚动
					},
					rail: {
						keepShow: true,
					},
					bar: {
						hoverStyle: true,
						onlyShowBarOnScroll: false, //是否只有滚动的时候才显示滚动条
						background: "hsla(0, 0%, 46%)", //滚动条颜色
						opacity: 0.7, //滚动条透明度
					},
				},
			}
		},
		mounted() {
			this.configurationHeight = window.configurationHeight
			let _this = this
			this.swiper = new Swiper(".swiper-container", {
				loop: false,
				navigation: {
					nextEl: '.swiper-button-next',
					prevEl: '.swiper-button-prev',
					hiddenClass: 'swiper-button-hidden',
				},
				on: {
					slidePrevTransitionStart: function(obj) {
						_this.activeIndex = obj.activeIndex
					},
					slideNextTransitionStart: function(obj) {
						_this.activeIndex = obj.activeIndex
						if (_this.detail.length === 1) {
							_this.swiper.slidePrev(0)
						}
					},
					transitionStart: function(obj) {
						_this.showTooltipMod = false
						if (_this.activeIndex == 0 && obj.activeIndex == 0) {
							_this.returnList()
						}
					},
				}
			})

			this.getdetail()
		},
		beforeDestroy() {
			if (this.swiper) {
				this.swiper.destroy();
				this.swiper = null;
			}
		},
		methods: {
			getWinInfo(win_info_display) {
				let winInfoList = [
					[false, false, false],
					[false, false, false, false],
					[false, false, false],
				]
				if (win_info_display.length) {
					win_info_display.map((detail_son, i) => {
						detail_son.winGold = this.formatArray(detail_son.win)
						detail_son.tWinGold = this.formatArray(detail_son.t_win)
						if (detail_son.line_id == 1) {
							winInfoList[0][0] = true
							winInfoList[1][0] = true
							winInfoList[2][0] = true
						} else if (detail_son.line_id == 2) {
							winInfoList[0][0] = true
							winInfoList[1][1] = true
							winInfoList[2][0] = true
						} else if (detail_son.line_id == 3) {
							winInfoList[0][0] = true
							winInfoList[1][1] = true
							winInfoList[2][1] = true
						} else if (detail_son.line_id == 4) {
							winInfoList[0][1] = true
							winInfoList[1][1] = true
							winInfoList[2][0] = true
						} else if (detail_son.line_id == 5) {
							winInfoList[0][1] = true
							winInfoList[1][1] = true
							winInfoList[2][1] = true
						} else if (detail_son.line_id == 6) {
							winInfoList[0][1] = true
							winInfoList[1][2] = true
							winInfoList[2][1] = true
						} else if (detail_son.line_id == 7) {
							winInfoList[0][1] = true
							winInfoList[1][2] = true
							winInfoList[2][2] = true
						} else if (detail_son.line_id == 8) {
							winInfoList[0][2] = true
							winInfoList[1][2] = true
							winInfoList[2][1] = true
						} else if (detail_son.line_id == 9) {
							winInfoList[0][2] = true
							winInfoList[1][2] = true
							winInfoList[2][2] = true
						} else if (detail_son.line_id == 10) {
							winInfoList[0][2] = true
							winInfoList[1][3] = true
							winInfoList[2][2] = true
						}
					})
				}
				return winInfoList
			},
			getPropsList(props_display) {
				if (props_display.length) {
					let propsList = [
						[],
						[],
						[]
					]
					let j = 0
					props_display.map((detail_son, i) => {
						let icon = ''
						if (i == 3 || i == 7) {
							j++
						}
						switch (detail_son) {
							case 1:
								icon = "fo-symbols-sprite h_ingots";
								break;
							case 2:
								icon = "fo-symbols-sprite h_chest";
								break;
							case 3:
								icon = "fo-symbols-sprite h_bag";
								break;
							case 4:
								icon = "fo-symbols-sprite h_packets";
								break;
							case 5:
								icon = "fo-symbols-sprite h_oranges";
								break;
							case 6:
								icon = "fo-symbols-sprite h_crackers";
								break;
							case 7:
								icon = "fo-dynamic-symbols-sprite s_wild";
								break;
						}
						propsList[j].push(icon)
					})
					return propsList
				}
			},
			getdetail() {
				let data = {
					token: localStorage.getItem("token"),
					game_id: localStorage.getItem("gameId"),
					order_no: localStorage.getItem("orderNo"),
				}
				this.$http.post('/api/order/get_detail',
					JSON.stringify(data)).then(response => {
					if (response.data.meta.code == 0) {
						let list = this.oxHistoryConvert(response.data.data[0])
						list.map(item => {
							item.localeTime = this.getLocaleTime(item.bet_time)
							item.win_info_display = item.win_info
							item.props_display = item.props
							item.betGold = this.formatArray(item.bet_gold)
							item.profitGold = this.formatArray(item.profit)
							item.endGold = this.formatArray(item.end_gold)
							item.betSize = this.formatArray(item.bet_size)
							item.totalWin = this.formatArray(item.total_win)
							// item.frees_display = JSON.parse(item.frees)
							item.win_mul_info_display = item.win_mul_info
							item.orderNo = this.getOrderNo(item.order_no)
							item.propsList = this.getPropsList(item.props_display)
							item.winInfoList = this.getWinInfo(item.win_info_display)
						})
						this.detail = list
						// 数据加载后，刷新轮播
						setTimeout(() => {
							this.swiper.update(true)
							this.activeIndex = 0
						}, 500)
					}
				});
			},
			oxHistoryConvert(data) {
				let list = [];
				let result = JSON.parse(data.remarks);
				for (let i = 0; i < result.length; i++) {
					let elem = result[i];
					let item = {};
					//图标
					item.props = elem.prop;

					//下注金额
					let bet_gold;
					if (i == 0) {
						bet_gold = data.bet_gold;
					} else {
						bet_gold = 0;
					}
					item.bet_gold = data.bet_gold;

					item.bet_mul = data.bet_mul;
					item.bet_size = data.bet_size;
					item.bet_time = data.bet_time;
					item.type = data.type;
					item.total_win = data.total_win;

					//余额
					let end_gold;
					if (i == result.length - 1) {
						end_gold = data.end_gold;
					} else {
						end_gold = data.end_gold - data.profit - data.bet_gold;
					}
					item.end_gold = end_gold;

					//订单号
					item.order_no = data.order_no;

					//盈利
					let profit;
					if (data.type == 1 || result.length == 1) {
						profit = data.profit;
					} else if (i == result.length - 1) {
						profit = data.profit + data.bet_gold;
					} else if (i == 0) {
						profit = -data.bet_gold;
					} else {
						profit = 0;
					}
					item.profit = profit;

					//倍数
					let win_arr = elem.win_arr;
					let win_mul = win_arr.length == 10 ? 10 : 1;
					item.win_mul = win_mul;

					//中奖线
					let win_info = [];
					for (let i = 0; i < win_arr.length; i++) {
						let info = win_arr[i];
						let t_win = parseFloat(info.tw) * 100;
						let win = t_win / 10;
						win_info.push({
							line_id: info.line,
							mul: parseFloat(info.odd),
							win: win,
							t_win: t_win,
						});
					}
					item.win_info = win_info;

					list.push(item);
				}
				return list;
			},
			showTooltip(item, i, obj) {
				this.mul = item.mul
				this.showTooltipMod = !this.showTooltipMod
				let x = obj.win_info_display.length - i - 1
				this.showTooltipTop = -48 * x
			},
			toIndex(i) {
				let _this = this
				// 延时触发v-if
				if (this.hidePopup) {
					setTimeout(function() {
						_this.hidePopup = false
					}, 750)
				} else {
					this.hidePopup = true
				}
				this.showPopup = !this.showPopup
				this.swiper.slideTo(i)
			},
			changePopup() {
				if (this.detail.length > 1) {
					let _this = this
					// 延时触发v-if
					if (this.hidePopup) {
						setTimeout(function() {
							_this.hidePopup = false
						}, 750)
					} else {
						this.hidePopup = true
					}
					this.showPopup = !this.showPopup
				}
			},
			returnList() {
				this.$parent.changeDetails()
			},
			swiperEnd(obj) {
				console.log(this.activeIndex, obj.activeIndex)
				if (obj.activeIndex == 0 && this.activeIndex == 0) {
					this.returnList()
				}
				this.activeIndex = obj.activeIndex
			},
			getOrderNo(order_no) {
				let orderNo = '-'
				if (order_no) {
					var firstPart = order_no.slice(0, 11); // 获取字符串的前n-1个字符
					var secondPart = order_no.slice(11); // 获取字符串的第n个字符及之后的字符
					orderNo = firstPart + " " + secondPart
				}
				return orderNo
			},
			formatArray(num) {
				let result = [];
				let reverseList = [];
				if (num != null) {
					let numStr = (num / Math.pow(10, 2)).toFixed(2);

					for (let i = numStr.length - 1; i < numStr.length && i >= 0; i--) {
						reverseList.push(numStr[i]);
					}

					for (let i = 0; i < reverseList.length; i++) {
						if (reverseList[i] == '.') {
							result.push(',');
						} else if (((i - (2 + 1)) % 3) === 0 && i > (2 + 1)) {
							if (reverseList[i] !== '-') {
								result.push('.');
							}
							result.push(reverseList[i]);
						} else {
							result.push(reverseList[i]);
						}
					}
				}
				return result.reverse().join('')
			},
			getLocaleTime(time) {
				let timeList = ['-', '-', '-']
				if (time) {
					let date = new Date(time * 1000)
					let yy = date.getFullYear();
					let mm = date.getMonth() + 1;
					mm = mm < 10 ? '0' + mm : mm;
					let dd = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
					let hh = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
					let mf = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
					let ss = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
					timeList[0] = mm + "/" + dd
					timeList[1] = hh + ":" + mf + ":" + ss
					timeList[2] = yy + "/" + mm + "/" + dd + " " + hh + ":" + mf + " " + this.timezone
				}
				return timeList
			},
		}
	}
</script>

<style>
	body {
		font-family: auto;
	}

	.result-title-title {
		color: #fff;
		font-size: 16px;
		text-align: center;
		margin-bottom: 5px;
		line-height: 20px;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: center;
		-ms-flex-align: center;
		align-items: center;
		-webkit-box-pack: center;
		-ms-flex-pack: center;
		justify-content: center;
		cursor: pointer;
	}

	.result-title-title span {
		margin-right: 10px;
	}

	.result-title-title .img {
		width: 18px;
		height: 18px;
		-webkit-mask: url("../assets/image/verify/arrows.png");
		mask: url("../assets/image/verify/arrows.png");
	}

	.swiper-button-hidden {
		opacity: 0;
		display: none !important;
	}

	.game-overlay {
		font-family: PingFang SC, Microsoft YaHei, WenQuanYi Micro Hei, sans-serif;
		touch-action: none;
	}

	.fo-dynamic-symbols-sprite {
		background-image: url("../assets/image/history/wild.png");
		background-repeat: no-repeat;
		display: inline-block;
		overflow: hidden
	}

	.s_wild {
		background-position: -1px -1px
	}

	.s_wild,
	.s_wild_red {
		height: 180px;
		width: 180px
	}

	.s_wild_red {
		background-position: -183px -1px
	}

	#game-free-spin-view-container {
		display: flex;
		flex-direction: column;
		font-size: 14px;
		height: inherit;
		position: absolute;
		top: 0;
		width: inherit
	}

	.game-free-spin-list-item {
		display: flex;
		height: 30px;
		padding: 10px;
		transition: background-color .1s ease-out
	}

	.game-free-spin-list-item:nth-child(odd) {
		background-color: #282834;
	}

	.game-free-spin-list-item:nth-child(2n) {
		background-color: #30303c;
	}

	.game-free-spin-type {
		padding-left: 15px;
		padding-top: 5px
	}

	.game-free-spin-amount {
		margin-left: auto;
		margin-right: 0;
		padding-right: 15px;
		padding-top: 5px
	}

	#close-list-button {
		align-items: center;
		display: flex;
		height: 50px;
		justify-content: center;
		transition: opacity .1s ease-out;
		width: inherit;
		border-top: 1px solid #30303c;
		border-bottom: 1px solid #30303c;
	}

	#close-list-button:active {
		opacity: .3
	}

	#nav-drop-down-arrow {
		transform: scale(.6) translateX(-5px)
	}

	#loading-exit.vertical {
		height: 32px;
		position: absolute;
		right: 15px;
		top: 13px;
		width: 32px
	}

	#loading-exit.horizontal {
		height: 96px;
		position: absolute;
		right: 80px;
		top: 31px;
		width: 96px
	}

	.exit-icon {
		align-items: center;
		display: flex;
		justify-content: center
	}

	.exit-icon.vertical {
		height: 32px;
		width: 32px
	}

	.exit-icon.horizontal {
		height: 96px;
		width: 96px
	}

	.exit-icon-stroke {
		position: absolute
	}

	.exit-icon-stroke-vertical {
		height: 26px;
		width: 1px
	}

	.exit-icon-stroke-horizontal {
		height: 68px;
		width: 3px
	}

	.exit-icon-stroke-one {
		transform: rotate(45deg)
	}

	.exit-icon-stroke-two {
		transform: rotate(-45deg)
	}

	#loading-exit.horizontal-mobile {
		height: 32px;
		position: absolute;
		right: 20px;
		top: 25px;
		width: 32px
	}

	.exit-icon-stroke-horizontal-mobile {
		height: 26px;
		width: 1px
	}

	.exit-icon.horizontal-mobile {
